import React, { useEffect, useState } from 'react';
import styles from './app.module.css';
import { WidgetBar } from './components/WidgetBar/WidgetBar';
import { CanvasBlock } from './components/CanvasBlock/CanvasBlock';
import { motion } from 'framer-motion';
const App = () => {
	const [bgColor, setBgColor] = useState('rgba(0, 0, 0, 0)');

	const handleScroll = () => {
		const scrollTop = window.scrollY;
		const maxScroll = 350; // Максимальная высота скролла для полного затемнения
		const opacity = Math.min(scrollTop / maxScroll, 1); // Ограничиваем значение до 1

		// Используем requestAnimationFrame для плавного обновления
		requestAnimationFrame(() => {
			setBgColor(`rgba(0, 0, 0, ${opacity})`);
		});
	};

	useEffect(() => {
		window.addEventListener('scroll', handleScroll);
		return () => {
			window.removeEventListener('scroll', handleScroll);
		};
	}, []);

	document.addEventListener('aos:in', ({ detail }) => {
		console.log('animated in', detail);
	});

	return (
		<>
			<motion.div initial={{ opacity: 0, y: 0 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 2 }}>
				<div className={styles.container}>
					<CanvasBlock />
					<div className={styles.overlayElement} style={{ background: bgColor }}></div>
					<div className={styles.widgetBlock}>
						{' '}
						<WidgetBar />
					</div>
				</div>
				<div className={styles.content}>
					<div className={styles.block3}>
						<div className={styles.photoBlock}>
							<img className={styles.image} src='/portfolio-site/images/1.png' alt='1' />
						</div>
						<div className={styles.textInfoBlock}>
							A full copied website of jewerly brand Homer founded by Frank Ocean in 2021 Was rebuiled on react instead
							of shopify
						</div>
					</div>
					<div className={styles.block3}>
						<div className={styles.photoBlock}>
							<img className={styles.image} src='/portfolio-site/images/3.png' alt='1' />
						</div>
						<div className={styles.textInfoBlock}>
							<div className={styles.textContent}>
								<p style={{ textDecoration: 'underline' }}>Title:</p>
								<p>Yung Lean Merchandise Store</p>
								<p style={{ textDecoration: 'underline' }}>Description: </p>
								<p>
									The Yung Lean merchandise store was completely rebuilt from scratch using React, with significant
									improvements to the user interface and user experience. <br />A Node.js + Express backend was
									developed along with a MongoDB database. The project features an enhanced UI/UX design, improved state
									management, and a more responsive, intuitive shopping experience.
								</p>
								<p style={{ textDecoration: 'underline' }}>Goal was pointed: </p>
								<p>
									{' '}
									explore and analyze a extremely simple interface, ways why it looks unique, the ways how it can be
									improoved and make more friendly for users{' '}
								</p>
								<p style={{ textDecoration: 'underline' }}>Developed project: </p>
								<p>https://danil-golubev.github.io/merchmarket-frontend/</p>
								<p style={{ textDecoration: 'underline' }}>Github repository:</p>
								<p>https://github.com/Danil-Golubev/merchmarket-frontend</p>
								<br />
							</div>
						</div>
					</div>
					<div className={styles.block2}>
						<div>page in progress, please comeback later</div>
					</div>
				</div>
			</motion.div>
		</>
	);
};

export default App;
